.header {
  height: 80px;
  width: 100vw;
  background-color: #ffc765;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 16px;
}

.header h1 {
  text-align: center;
  font-size: 32px;
  color: #2b1f9e;
  margin: 0px;
  width: max-content;
}

.header a:not(:last-child) {
  margin-right: 8px;
}

.page {
  max-width: 1000px;
  margin: 0 auto;
  padding:0 16px;
}